@import url(https://fonts.googleapis.com/css?family=Shadows+Into+Light&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  background: #282c34 no-repeat center center fixed;
  background-size: cover;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.nav-white {
  color: #fff;
}

.header-text {
  padding-left: 10px;
  font-family: "Shadows Into Light", cursive;
}
.logo-img {
  height: auto;
  width: auto;
  max-width: 50px;
  max-height: 50px;
}

.homeLogo {
  max-width: 200px;
  max-height: 200px;
}

.env-size {
  font-size: 30px;
}
.mail-card {
  padding-bottom: 10px;
}

.b-background {
  background-color: #282c34;
  color: white;
}

/* This a is used at about page*/
.a {
  text-decoration: none;
  color: white;
}
a:hover {
  text-decoration: none;
}

.card-font {
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #777;
}

#team img {
  margin-top: -15px;
}

#team i {
  font-size: 26px;
  color: #555;
  margin-top: -40px;
}

#team p {
  font-weight: 500;
}

#team .card {
  background-color: #ffffff;
  border-radius: 0;
  box-shadow: 5px 5px 15px wheat;
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  -webkit-transition: all 0.3 ease-in;
  -moz-transition: all 0.3 ease-in;
  margin: 0 auto; /* Added */
  float: none; /* Added */
  margin-bottom: 15px; /* Added */
}

#team .card:hover {
  background: #c0c0c0;
  color: #fff !important;
  border-radius: 5px;
  border: none;
  box-shadow: 5px 5px 10px white;
}

#team .card:hover h3,
#team .card:hover i,
#team .card:hover h5,
#team .card:hover p {
  color: #fff !important;
}

.job-btn {
  position: relative;
  top: 40%;
  /*
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  */
  background-color: #555;
  color: white;
  border: none;
}

.text-jobs {
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.jobs-top {
  height: 400px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(0, 0, 0, 0.2)),
      to(rgba(0, 0, 0, 0.4))
    ),
    url(/punipuni-site/static/media/job-top.b861ac79.png);
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0.4)
    ),
    url(/punipuni-site/static/media/job-top.b861ac79.png);
}

@media (max-width: 767px) {
  .home-text {
    font-size: 30px;
  }
  .home-p {
    font-size: 15px;
  }

  #team .card {
    max-width: 250px;
    max-height: 330px;
  }

  .homeLogo {
    max-width: 150px;
  }
}

@media (min-width: 1200px) {
  .home-text {
    font-size: 40px;
  }
  .home-p {
    font-size: 20px;
  }

  #team .card {
    max-width: 300px;
    max-height: 350px;
  }

  .sliderDiv {
    width: 70%;
    margin-top: 5px;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  #team .card {
    max-width: 300px;
    max-height: 350px;
  }
}

